export default "import EmblaCarousel from 'embla-carousel';\nimport { addThumbButtonClickHandlers, addToggleThumbButtonsActive } from './EmblaCarouselThumbsButton';\nimport '../css/base.css';\nimport '../css/sandbox.css';\nimport '../css/embla.css';\n\nconst OPTIONS = {};\nconst OPTIONS_THUMBS = {\n    containScroll: 'keepSnaps',\n    dragFree: true\n};\n\nconst viewportNodeMainCarousel = (document.querySelector('.embla__viewport'));\nconst viewportNodeThumbCarousel = (document.querySelector('.embla-thumbs__viewport'));\nconst emblaApiMain = EmblaCarousel(viewportNodeMainCarousel, OPTIONS);\nconst emblaApiThumb = EmblaCarousel(viewportNodeThumbCarousel, OPTIONS_THUMBS);\n\naddThumbButtonClickHandlers(emblaApiMain, emblaApiThumb);\naddToggleThumbButtonsActive(emblaApiMain, emblaApiThumb);\n";